import Vue from "vue";
import VueRouter from "vue-router";

// Progress bar
import NProgress from "nprogress";
import "nprogress/nprogress.css";

import Home from "../views/Home.vue";
import About from "../views/About.vue";
import Works from "../views/Works.vue";
import Contact from "../views/Contact.vue";
import WebDev from "../views/WebDev.vue";
import AskService from "../views/AskService.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home
  },
  {
    path: "/about",
    name: "about",
    component: About
  },
  {
    path: "/works",
    name: "works",
    component: Works
  },
  {
    path: "/contact",
    name: "contact",
    component: Contact
  },
  {
    path: "/webDev",
    name: "webDev",
    component: WebDev
  },
  {
    path: "/askService",
    name: "askService",
    component: AskService
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes,
  scrollBehavior() {
    document.getElementById('app').scrollIntoView();
  }
});

router.beforeResolve((to, from, next) => {
  if (to.path) {
    NProgress.start()
  }
  next()
});

router.afterEach(() => {
  NProgress.done()
});

export default router;
